@import '../../../sass/colors';

.chooseGroup-top {
  display: flex;
  flex-direction: row;
  max-width: 700px;

  .searchGroup {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    margin-right: 20px;

    label {
      margin: 0 10px 0 0;
      flex-shrink: 0;
    }
    h4 {
      margin: 0;
    }
  }
  .user-more {
    cursor: pointer;
    color: $cBlack2HoverNo;
    position: relative;

    &:hover {
      color: $cBlack2HoverYes;
    }
  }
}